import { Component, OnInit , ViewChild} from '@angular/core';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { critere } from './critere.model';
import { SkylineService } from 'src/app/services/skyline.service';

import {
  ChartComponent,
  ApexAxisChartSeries,
  ApexChart,
  ApexYAxis,
  ApexXAxis,
} from "ng-apexcharts";

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  xaxis: ApexXAxis;
  yaxis: ApexYAxis;
};

@Component({
  selector: 'app-skyline',
  templateUrl: './skyline.component.html',
  styleUrls: ['./skyline.component.css']
})
export class SkylineComponent implements OnInit {
  
  @ViewChild("chart") chart: ChartComponent;
  public chartOptions: Partial<ChartOptions>;
  
  skylineform: FormGroup;
  arrCr = [];
  critere = new critere(); 
   flag = false ;
   flagGraph = false;
   cont = 0;
   public skys;
   data = [];
   data2 = []; 
   data3 = [] ;
   all = [];
   set = false;
   temps ;
   algoName;
  constructor(private skylineService: SkylineService) { 
    
  }

  ngOnInit(): void {
    this.skylineform = new FormGroup({
      dim: new FormControl('', Validators.required),
      cho1: new FormControl('', Validators.required),
      cho2: new FormControl('', Validators.required),
      cho3: new FormControl('', Validators.required),
      cho4: new FormControl('', Validators.required),
      critere0: new FormControl('', Validators.required),
      critere1: new FormControl('', Validators.required),
      critere2: new FormControl('', Validators.required),
      critere3: new FormControl('', Validators.required),
      algo: new FormControl('', Validators.required),
    });
   
  }

  addForm(){  
    if(this.flag) {
      this.arrCr = [];
      this.flag = false;
  }
    for(var j = 0 ; j < this.skylineform.get('dim').value ; j++){
      this.arrCr.push(new critere());
      this.flag = true;
      this.cont = j;
      }
    
     
  }

  submitRegistration(){
  
    if(this.set == true){
      this.skylineService.getData(this.skylineform.get('cho1').value,this.skylineform.get('cho2').value).subscribe(  
        data => { this.data3.push(data);
       for(let i=0 ; i < this.data3['0'].length ; i++){
      this.all.push(data[i].dims.map(res => res));
    }
    console.log('all');
    console.log(this.all.length);
   },
     err => console.log(err),
     () => console.log('apps loaded'),
    );
    }
    if(this.skylineform.get('dim').value == 2){
      this.set = true;
      this.skylineService.getSky2D(this.skylineform.get('algo').value,
      this.skylineform.get('cho1').value,this.skylineform.get('critere0').value,
      this.skylineform.get('cho2').value, this.skylineform.get('critere1').value,
      ).subscribe(
        data => { this.skys = data; this.data2.push(data);
        
          for(let i=0 ; i < this.skys.length ; i++){
         this.data.push(this.skys[i].dims.map(res => res));
       }
       console.log("data");
       console.log(this.data.length);
       console.log("skys");
       console.log(this.skys.length);

      },
        err => console.log(err),
        () => console.log('apps loaded'),
       
      );
	  
	   this.skylineService.getTemps().subscribe(
        data => { this.temps = data;}
	   )
      this.chartOptions = {
        series: [
          {
          name: "Skyline",
          data: this.data
        },
        {
          name: "Data",
          data: this.all
        }],
        
        chart: {
          height: 350,
          type: 'scatter',
          zoom: {
            enabled: true,
            type: 'xy'
          }
        },
        xaxis: {
          tickAmount: 10,
          labels: {
            formatter: function(val) {
              return parseFloat(val).toFixed(1)
            }
          }
        },
        yaxis: {
          tickAmount: 7
        }
      };
      this.flagGraph = true; 
    }
    if(this.skylineform.get('dim').value == 3){
      this.skylineService.getSky3D(this.skylineform.get('algo').value,
      this.skylineform.get('cho1').value,this.skylineform.get('critere0').value,
      this.skylineform.get('cho2').value, this.skylineform.get('critere1').value,
      this.skylineform.get('cho3').value,this.skylineform.get('critere2').value 
      ).subscribe(
        data => { this.skys = data },
        err => console.log(err),
        () => console.log('apps loaded')
      );
      this.flagGraph = false; 

    }
    if(this.skylineform.get('dim').value == 4){
      console.log(this.skylineform.get('critere0').value);   
      this.skylineService.getSky4D(this.skylineform.get('algo').value,
      this.skylineform.get('cho1').value,this.skylineform.get('critere0').value,
      this.skylineform.get('cho2').value, this.skylineform.get('critere1').value,
      this.skylineform.get('cho3').value,this.skylineform.get('critere2').value,
      this.skylineform.get('cho4').value, this.skylineform.get('critere3').value).subscribe(
        data => { this.skys = data },
        err => console.log(err),
        () => console.log('apps loaded')
      );
      this.flagGraph = false; 
    }

    this.data = [];
    this.all = [];

	this.skylineService.getTemps().subscribe(
        data => { this.temps = data;}
	   )
  

    }

    
  
}
